import React, { useState } from "react";
import { Button, Box, TextField, FormControl, Select, MenuItem, Chip } from '@material-ui/core'
import { contents } from "../contents";

const defaultValues = {
    topic: '',
    contents: {
        subject: '',
        content: ''
    },
    students: {
        name: '',
        class: 0
    },
    date: '',
    duration: ''
}
console.log('defaultValues: ', defaultValues)

const LessonForm = () => {
    const [formValues, setFormValues] = useState(defaultValues)
    const [filteredContents, setFilteredContents] = useState(contents)

    console.log('formValues: ', formValues)

    let newContents

    const handleChange = (event) => {
      const {
        target: { name, value },
      } = event;
      console.log('name & value: ', name, value)
      if(value === 'maths') {
        setFilteredContents(contents.filter(c => c === 'computer games'))
        
    console.log('contents after if: ', newContents)
    }
      setFormValues({
          ...formValues,
          contents: {
              ...formValues.contents,
              [name]: typeof value === 'string' ? value.split(',') : value
          }
      }
      );
    };

    const handleSubmit = (event) => {
        event.preventDefault()
        console.log(formValues)
    }

    return (
        <form onSubmit={handleSubmit} >
            <Box display='flex' flexDirection='column'>
                <Box display='flex' flexDirection='column' justifyContent='flex-start' padding='20px'>
                    <Box padding='20px'>
                        Topic
                        <TextField 
                            id='topic-input'
                            label='topic'
                            variant='filled'
                            autoFocus
                            required 
                            fullWidth
                            className='topic'
                        />
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Subject
                        <FormControl>
                            <Select
                                MenuProps={{
                                    anchorOrigin: {
                                    vertical: "bottom",
                                    horizontal: "left"
                                    },
                                    transformOrigin: {
                                    vertical: "top",
                                    horizontal: "left"
                                    },
                                    getContentAnchorEl: null
                                }}
                                value={formValues.contents.subject}
                                onChange={handleChange}
                                name='subject'
                            >
                                <MenuItem key='finnish' value='finnish'>
                                    Finnish
                                </MenuItem>
                                <MenuItem key='maths' value='maths'>
                                    maths
                                </MenuItem>
                                <MenuItem key='science' value='science'>
                                    science
                                </MenuItem>
                                <MenuItem key='religion' value='religion'>
                                    religion
                                </MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Content
                        <FormControl sx={{ m: 1, width: 300 }}>
                            <Select
                                MenuProps={{
                                    anchorOrigin: {
                                    vertical: "bottom",
                                    horizontal: "left"
                                    },
                                    transformOrigin: {
                                    vertical: "top",
                                    horizontal: "left"
                                    },
                                    getContentAnchorEl: null
                                }}
                                labelId="demo-multiple-chip-label"
                                id="demo-multiple-chip"
                                multiple
                                name='content'
                                value={formValues.contents.content ? formValues.contents.content : []}
                                onChange={handleChange}
                                renderValue={(selected) => (
                                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                    {selected.map((value) => (
                                        <Chip key={value} label={value} />
                                    ))}
                                    </Box>
                                )}
                                >
                                {filteredContents.map((name) => (
                                    <MenuItem
                                        key={name}
                                        value={name}
                                    >
                                        {name}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Box>
                </Box>
            </Box>
            <Box display='flex' justifyContent='center' paddingTop='20%'>
                <Button variant='contained' type='submit'>
                    Submit
                </Button>
            </Box>
        </form>
    )
    
}

export default LessonForm